a {
  color: #7F7FFF;
}

a:visited {
  color: #A77DCE;
}

.App {
  text-align: center;
}

.App-footer {
  background-color: #282c34;
  height: 8vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 2em;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-menu {
  display: flex;
  flex-grow: 1;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.5em;
}

.App-menu a {
  text-decoration: none;
  color: white;
}

.App-logo {
  height: 4vh;
}

.App-link {
  color: #61dafb;
}

.App-content {
  color: white;
  background: linear-gradient(#14171f, #1d212c);
  height: 92vh;
  padding: 0 calc(0.5rem + 1vmin);
  overflow-y: auto;
  box-sizing: border-box;
}

.App-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 92vh;
  box-sizing: border-box;
}

.App-bigtext {
  font-size: calc(18px + 2vmin);
}

.App-slim {
  width: 90%;
}

.My-name {
  color: #5f77ff;
}

.My-school {
  color: #ff5f6c;
}

.My-programme {
  color: #cd91ff;
}

.text-i {
  font-style: italic;
}

.inline {
  display: inline-block;
}